<template>
  <v-app>
    <Header />
    <v-content> <v-btn fab color="cyan accent-2" top left absolute style="z-index:100" @click="dialog = true"><v-icon>add</v-icon> </v-btn> <v-container>
      
      <v-data-table
      :headers="headers"
      :items="users"
      :items-per-page="10"
      class="elevation-1 mt-3"
      :loading=isloading loading-text="Loading ...."
    >
    <template v-slot:item.passwd="{ item }">
      <v-row>
        <v-col cols="12" md="10" >
          <v-text-field  v-model=item.passwd :loading="item.isupdated" />
        </v-col>
        <v-col  cols="12" md="2">
          <v-btn v-show="!item.isupdated" color="green" dark large @click="updatePassword(item)"> <v-icon dark>mdi-pencil</v-icon> </v-btn>
        </v-col>
      </v-row>
    </template>
    <template v-slot:item.id="{ item }" >
      <v-btn fab dark small color="error" v-show="item.id != 1">
        <v-icon dark @click="hapusUser(item)">delete</v-icon>
      </v-btn>
    </template>
    </v-data-table>
    </v-container></v-content>
    <Footer />
    
    <v-dialog
        v-model="dialog" persistent
        max-width="500px"
      >
        <v-card>
          <v-card-title>
            Tambah user
          </v-card-title>
          <v-card-text>
            <v-form ref="addUserForm" v-model="valid">
            <v-container>
              <v-row>
                <v-col>
                  <v-text-field label="User name*" :rules="requiredRules" required v-model="username"></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <v-text-field label="Password*" :rules="requiredRules" required v-model="passwd"></v-text-field>
                </v-col>
              </v-row>
            </v-container>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn color="blue darken-1" text @click="addUser()">Simpan</v-btn>
            <v-btn color="blue darken-1" text @click="dialog = false">Tutup</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    
  </v-app>
</template>

<script>
  import Header from './adminlayout/Header';  
  import Footer from './adminlayout/Footer';  
  
  export default {
    components: {
      Header, Footer
    },
    data: () => ({
      valid: true,
      isloading:true,
      dialog:false,
      title:'Users',
      color:'blue',
      username:'',passwd:'',
      requiredRules: [
        v => !!v || 'Harus di isi',
      ],
      headers:[
        { text: 'User name', value: 'username' }
        ,{ text: 'Password', value: 'passwd' }
        ,{ text: '', value: 'id' }
      ],
      users:[],
    }),
    methods: {
      refreshUsers() {
        var vue = this;
        vue.isloading = true;
        fetch(process.env.VUE_APP_API_URL+'api/data-users').then(response => response.json()).then(data => {
          vue.users = data;
          vue.isloading = false;
        });
      },
      updatePassword(item) {
        item.isupdated = true;
        
        var formData = new FormData();
        formData.append('id', item.id);
        formData.append('passwd', item.passwd);
        fetch(process.env.VUE_APP_API_URL+'update-password', {  body: formData, method: 'POST' }).then(response => response.json()).then(data => {
            item.isupdated = false;
        });
      },
      addUser() {
        var vueku = this;
        if (vueku.$refs.addUserForm.validate()) {
          var formData = new FormData();
          formData.append('passwd', vueku.passwd);
          formData.append('username', vueku.username);
          fetch(process.env.VUE_APP_API_URL+'add-user', {  body: formData, method: 'POST' }).then(response => response.json()).then(data => {
            vueku.dialog = false;
            vueku.refreshUsers();
          });
        }
      },
      hapusUser(item) {
        var usersnya = this.users;
        var formData = new FormData();
        formData.append('id', item.id);
        fetch(process.env.VUE_APP_API_URL+'delete-an-user', {  body: formData, method: 'POST' });
        usersnya.splice( usersnya.indexOf(item), 1 );
      }
    },
    created() {
      this.$store.state.adminTitle = this.title;
      this.$store.state.adminColor = this.color;
      this.refreshUsers();
    }
  }
</script> 
